// Member

.members {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, auto));
  justify-content: space-between;
  grid-gap: 40px;
  .member {
    display: flex;
    flex-direction: column;
    align-items: center;    
    padding: 10px;
    .image {
      width: 100%;
      height: 0;
      padding-bottom: 100%;
      border-radius: 50%;
      overflow: hidden;
      margin-bottom: 20px;
    }
    .name {
      font-weight: bolder;
    }
    .position {
      font-size: 0.875rem;
      margin-bottom: 8px;
    }
    .mail {
      font-size: 0.875rem;
    }
  }
}

